import React, { Component } from "react";
import { reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { berhasil, gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPut } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import {
  getValidasiReparasi,
  getValidasiReparasiSelected,
} from "../actions/validasiReparasi";
import { formatOptional } from "../helper";

const maptoState = (state) => {
  return {
    listValidasiCustomer: state.provinsi.listValidasiCustomer,
    ValidasiReparasiSelected: state.provinsi.ValidasiReparasiSelected,
    listCustomer: state.provinsi.customer,
    ValidasiReparasi: state.provinsi.ValidasiReparasi,
  };
};
class validasiReparasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "nama_toko",
          text: "Nama Toko",
        },
        {
          dataField: "nama_sales",
          text: "Nama Sales",
        },
        {
          dataField: "level_customer",
          text: "Level Customer",
        },

        {
          dataField: "total_bruto",
          text: "Total Bruto",
          align: "right",
          headerAlign: "right",
          formatter: (data) => {
            return formatOptional(data);
          },
        },
        {
          dataField: "total_ongkos_reparasi",
          text: "Total Ongkos Reparasi",
          align: "right",
          headerAlign: "right",
          formatter: (data) => {
            return formatOptional(data);
          },
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
    };
  }
  kirimvalidasi() {
    console.log("BERHASIL");
    var raw = JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
    var hasil = {
      detail_trx: raw,
    };
    AxiosMasterPut("reparasi/put/validate", hasil)
      .then(() => berhasil("Berhasil Validasi"))
      .then(() => this.prevStep())
      .then(() => this.props.dispatch(getValidasiReparasi()))
      .then(() => localStorage.removeItem("FakturTerpilih"))
      .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
      .catch((err) =>
        gagal(
          `Gagal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
        )
      );
  }

  batalvalidasi() {
    var raw = JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
    var hasil = {
      detail_trx: raw,
    };
    AxiosMasterPut("reparasi/put/batal-validate", hasil)
      .then(() => berhasil("Berhasil Batal Validasi"))
      .then(() => this.prevStep())
      .then(() => this.props.dispatch(getValidasiReparasi()))
      .then(() => localStorage.removeItem("FakturTerpilih"))
      .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
      .catch((err) =>
        gagal(
          `Gagal Batal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
        )
      );
  }

  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getValidasiReparasiSelected());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };

  componentDidMount() {
    localStorage.setItem("FakturTerpilih_kirim", "[]");
    localStorage.setItem("FakturTerpilih", "[]");
    this.props.dispatch(getValidasiReparasi());
  }
  // ONCHANGE PILIH KOTA
  onSubmitToko(data) {
    this.props.change("namaToko", data);
  }
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        let array2 =
          JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
        const data = {
          no_bon: row.no_bon,
          nama_toko: row.nama_toko,
          nama_sales: row.nama_sales,
          level_customer: row.level_customer,
          total_bruto: row.total_bruto,
          total_ongkos_reparasi: row.total_ongkos_reparasi,
        };
        const kirim = {
          no_trx: row.no_trx,
        };
        if (isSelect) {
          var index1 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          var index2 = array2.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          if (index1 < 0) {
            array.push(data);
            array2.push(kirim);
          } else {
            array.splice(index1, 1);
            array2.splice(index2, 1);
          }
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          var index = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          var index3 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          array.splice(index, 1);
          array2.splice(index3, 1);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        var array2 = [];
        rows.forEach(function (list) {
          const data = {
            no_bon: list.no_bon,
            nama_toko: list.nama_toko,
            nama_sales: list.nama_sales,
            level_customer: list.level_customer,
            total_bruto: list.total_bruto,
            total_ongkos_reparasi: list.total_ongkos_reparasi,
          };
          const kirim = {
            no_trx: list.no_trx,
          };
          array.push(data);
          array2.push(kirim);
        });
        if (isSelect) {
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          localStorage.removeItem("FakturTerpilih");
          localStorage.removeItem("FakturTerpilih_kirim");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Validasi Reparasi" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="no_bon"
                        data={this.props.ValidasiReparasi}
                        columns={this.state.columns}
                        search
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                        selectRow={selectRow}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.ValidasiReparasiSelected ? (
                        <ToolkitProvider
                          keyField="noFaktur"
                          data={this.props.ValidasiReparasiSelected}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.prevStep()}
                            >
                              <i className="fa fa-chevron-left mr-3"></i>
                              Kembali
                            </button>
                          </div>
                        </div>
                        <div className="ml-auto mr-3">
                          <div className="row">
                            <button
                              className="btn btn-danger"
                              onClick={() => this.batalvalidasi()}
                            >
                              <i className="fa fa-times mr-3"></i>
                              BATAL VALIDASI
                            </button>
                            <button
                              className="btn btn-primary ml-2"
                              onClick={() => this.kirimvalidasi()}
                            >
                              <i className="fa fa-check mr-3"></i>
                              VALIDASI
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiReparasi = reduxForm({
  form: "validasiReparasi",
  enableReinitialize: true,
})(validasiReparasi);
export default connect(maptoState, null)(validasiReparasi);
